import React, { Component } from 'react'
import styled from '@emotion/styled'
import Modal from 'react-modal'

import Introduction from './IntroductionPanel'
import UnderstandingCongestion from './UnderstandingCongestionPanel'
import Scenarios from './ScenariosPanel'
import TakingAction from './TakingActionPanel'
import ReferencesIcon from '../ReferencesIcon'
import ReferencesModal from '../ReferencesModal'
import ArupLogo from '../ArupLogo'
import referencesContent from '../References/Content'

import { WIDTH, structure, contents } from '../../constants/config'

const Container = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  width: ${props => props.width}px;
  height: 100%;
  padding: 1rem 2rem 1rem;
  display: flex;
  flex-direction: column;
`

const FlexGrow = styled.div`
  flex-grow: 2;
`
//this uses flexbox as it will also be for the twitter share icon
const IconsContainer = styled.div`
  display: flex;
  justify-content: flex-end;
  padding-bottom: 1rem;
  cursor: pointer;
`
const LogoContainer = styled.div`
  display: flex;
  justify-content: flex-end;
  padding: 1rem 0;
`
const items = [
  { El: Introduction, title: 'Introduction', key: 'I', quickStep: 0 },
  {
    El: UnderstandingCongestion,
    title: 'Understanding Congestion',
    key: 'C',
    quickStep: contents.indexOf('Congestion'),
  },
  {
    El: Scenarios,
    title: 'Scenarios',
    key: 'S',
    quickStep: contents.indexOf('Doomsday_1'),
  },
  {
    El: TakingAction,
    title: 'Taking Action',
    key: 'A',
    quickStep: contents.indexOf('Taking Action'),
  },
]

const referencesChapters = [
  'Baseline occupancy',
  'Demographics',
  'Shared services',
  'Demand response',
  'Road user charging',
  'Road capacity',
  'Autonomous vehicles',
  'Headway impact',
  'Induced demand',
]

Modal.setAppElement('#root')
class SidePanel extends Component {
  state = {
    modalIsOpen: false,
  }

  openModal = () => {
    return this.setState({ modalIsOpen: true })
  }

  closeModal = () => {
    this.setState({ modalIsOpen: false })
  }

  render() {
    const {
      chapter,
      selectedSection,
      size,
      step,
      nextStep,
      prevStep,
      toStep,
    } = this.props
    return (
      <Container width={WIDTH}>
        <FlexGrow>
          <LogoContainer>
            <ArupLogo />
          </LogoContainer>
          {items.map(f => {
            const { El, title, key, quickStep } = f
            const selected = selectedSection === title
            const chapters = structure[title]
            return (
              <El
                key={`${key}-sidesection`}
                title={title}
                selected={selected}
                chapters={chapters}
                selectedChapter={chapter}
                size={size}
                step={step}
                nextStep={nextStep}
                prevStep={prevStep}
                quickStep={quickStep}
                toStep={toStep}
              />
            )
          })}
        </FlexGrow>
        {selectedSection === 'Understanding Congestion' &&
          (referencesChapters.indexOf(chapter) > -1 &&
            (referencesContent && (
              <IconsContainer>
                <div onClick={this.openModal}>
                  <ReferencesIcon />
                </div>
                <ReferencesModal
                  isOpen={this.state.modalIsOpen}
                  onRequestClose={this.closeModal}
                  contentLabel="Example Modal"
                  referencesContent={referencesContent[chapter]}
                ></ReferencesModal>
              </IconsContainer>
            )))}
      </Container>
    )
  }
}
export default SidePanel
