import React, { memo, lazy, Suspense } from 'react'
import Loading from '../../common/Loading'

const ScenarioSvgItem = memo(({ position, data, selectedScenario }) => {
  const SvgComponent = lazy(() => import(`../../svgs/${position}`))
  const x = `${data.x}px`
  const y = `${data.y}px`

  const scenario =
    data.scenario && data.scenario[selectedScenario]
      ? data.scenario[selectedScenario]
      : null

  return (
    <Suspense fallback={<Loading />}>
      <SvgComponent x={x} y={y} scenario={scenario} />
    </Suspense>
  )
})

export default ScenarioSvgItem
